  decrement() {
    if (this.state.i > 0) {
      this.setState({
        i: this.state.i - 1
      })
    } else {
      this.setState({
        i: 9
      })
    }
  }

  increment() {
    if (this.state.i < 9) {
      this.setState({
        i: this.state.i + 1
      })
    } else {
      this.setState({
        i: 0
      })
    }
  }

  spinLeft() {
    const sl = document.getElementById('sl')
    if(sl.onclick){
      let x = this.state.i.value
      this.setState({
        i: x++
      })
    }
  }

